<template>
  <div id="app" class="container">
    <GlobalHeader />
    <Memos />
  </div>
</template>

<script>
import GlobalHeader from './components/GlobalHeader'
import Memos from './components/Memos'
export default {
  name: 'app',
  components: {
    GlobalHeader,
    Memos
  }
}
