<style>
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  background-color: #E8E8E8;
  color: #4E4E4E;
  font-weight: 100;
  line-height: 1.4;
  font-family: "Hiragino Sans", "Hiragino Kaku Gothic ProN","メイリオ", sans-serif;
}

.container {
  padding: 10px;
}

.header {
  margin-left: 10px;
}

.header h1 {
  margin: 5px 0 10px
}

.add-button {
  margin-left: 10px;
  margin-bottom: 10px;
  font-size: 20px;
  padding: 5px 20px;
}

.memo-list {
  width: 100%;
}

.memo-item {
  display: inline-block;
  position: relative;
  background-color: #FEF3A1;
  min-height: 200px;
  width: 300px;
  border: 1px solid rgba(200, 200, 200, 0.2);
  box-shadow: 1px 1px 2px 0 rgba(0, 0, 0, 0.2);
