  .memo-item {
    width: calc(100% - 45px);
  }
}

.memo-item .remove-button {
  display: none;
  position: absolute;
  top: 3px;
  right: 5px;
  font-size: 24px;
  text-decoration: none;
}

.memo-item:hover {
  box-shadow: 1px 1px 3px 3px rgba(0,0,0,0.1);
}

.memo-item:hover .remove-button {
